/**
 * The ReactDOMServer object enables you to render components to static markup. Typically, it’s used on a Node server.
 *
 * Render a React element to its initial HTML. React will return an HTML string. You can use renderToString() method to generate HTML on the server and send the markup down on the initial request for faster page loads and to allow search engines to crawl your pages for SEO purposes.
 *
 * Syntax: ReactDOMServer.renderToString(element)
 *
 *<StaticRouter> is used to render a React Router web app in node. Provide the current location via the location prop.

 <StaticRouter location> defaults to "/"

 The Node.js fs module:
 The fs module provides a lot of very useful functionality to access and interact with the file system.
This module is a part of the Node.js core.

The fs.readFileSync() method is an inbuilt application programming interface of fs module which is used to read the file and return its content. 
In fs.readFile() method, we can read a file in a non-blocking asynchronous way, but in fs.readFileSync() method, we can read files in a synchronous way, i.e. we are telling node.js to block other parallel process and do the current file reading process
 *
 */
import express from "express";
import { renderToString } from "react-dom/server";
import { StaticRouter } from "react-router-dom/server";
import fs from "fs";
import App from "../src/App";
/**
 * We'll be listening on port 3000 (http://locahost:**3000**) unless a environment variable is passed in saying otherwise. We do this because if you try to deploy this, you'll need to watch for PORT.
 */
const PORT = process.env.PORT || 3000;

const html = fs.readFileSync("dist/frontend/index.html").toString();

const parts = html.split("not rendered");

const app = express();

app.use("/frontend", express.static("dist/frontend"));

app.use((req, res) => {
  const reactMarkup = (
    <StaticRouter location={req.url}>
      <App />
    </StaticRouter>
  );
  res.send(`${parts[0]}${renderToString(reactMarkup)}${parts[1]}`);
  res.end();
});

console.log(`listening on http://localhost:${PORT}`);
app.listen(PORT);
